				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="场馆名称" prop="changguanmingcheng" >
					<el-input v-model="ruleForm.changguanmingcheng" placeholder="场馆名称" clearable  :readonly="ro.changguanmingcheng"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="场馆名称" prop="changguanmingcheng" >
					<el-input v-model="ruleForm.changguanmingcheng" placeholder="场馆名称" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="场馆类型" prop="changguanleixing" >
					<el-input v-model="ruleForm.changguanleixing" placeholder="场馆类型" clearable  :readonly="ro.changguanleixing"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="场馆类型" prop="changguanleixing" >
					<el-input v-model="ruleForm.changguanleixing" placeholder="场馆类型" readonly></el-input>
				</el-form-item>
				<el-form-item class="upload" v-if="type!='info' && !ro.changguantupian" label="场馆图片" prop="changguantupian" >
					<file-upload
						tip="点击上传场馆图片"
						action="file/upload"
						:limit="3"
						:multiple="true"
						:fileUrls="ruleForm.changguantupian?ruleForm.changguantupian:''"
						@change="changguantupianUploadChange"
					></file-upload>
				</el-form-item>
				<el-form-item class="upload" v-else-if="ruleForm.changguantupian" label="场馆图片" prop="changguantupian" >
					<img v-if="ruleForm.changguantupian.substring(0,4)=='http'" class="upload-img" style="margin-right:20px;" v-bind:key="index" :src="ruleForm.changguantupian.split(',')[0]" width="100" height="100">
					<img v-else class="upload-img" style="margin-right:20px;" v-bind:key="index" v-for="(item,index) in ruleForm.changguantupian.split(',')" :src="$base.url+item" width="100" height="100">
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="场馆位置" prop="changguanweizhi" >
					<el-input v-model="ruleForm.changguanweizhi" placeholder="场馆位置" clearable  :readonly="ro.changguanweizhi"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="场馆位置" prop="changguanweizhi" >
					<el-input v-model="ruleForm.changguanweizhi" placeholder="场馆位置" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="场馆费用" prop="changguanfeiyong" >
					<el-input v-model.number="ruleForm.changguanfeiyong" placeholder="场馆费用" clearable  :readonly="ro.changguanfeiyong"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="场馆费用" prop="changguanfeiyong" >
					<el-input v-model="ruleForm.changguanfeiyong" placeholder="场馆费用" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="管理账号" prop="guanlizhanghao" >
					<el-input v-model="ruleForm.guanlizhanghao" placeholder="管理账号" clearable  :readonly="ro.guanlizhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="管理账号" prop="guanlizhanghao" >
					<el-input v-model="ruleForm.guanlizhanghao" placeholder="管理账号" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="管理姓名" prop="guanlixingming" >
					<el-input v-model="ruleForm.guanlixingming" placeholder="管理姓名" clearable  :readonly="ro.guanlixingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="管理姓名" prop="guanlixingming" >
					<el-input v-model="ruleForm.guanlixingming" placeholder="管理姓名" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="时间段" prop="shijianduan" >
					<el-input v-model="ruleForm.shijianduan" placeholder="时间段" clearable  :readonly="ro.shijianduan"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="时间段" prop="shijianduan" >
					<el-input v-model="ruleForm.shijianduan" placeholder="时间段" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="账号" prop="zhanghao" >
					<el-input v-model="ruleForm.zhanghao" placeholder="账号" clearable  :readonly="ro.zhanghao"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="账号" prop="zhanghao" >
					<el-input v-model="ruleForm.zhanghao" placeholder="账号" readonly></el-input>
				</el-form-item>
				<el-form-item class="input" v-if="type!='info'"  label="姓名" prop="xingming" >
					<el-input v-model="ruleForm.xingming" placeholder="姓名" clearable  :readonly="ro.xingming"></el-input>
				</el-form-item>
				<el-form-item v-else class="input" label="姓名" prop="xingming" >
					<el-input v-model="ruleForm.xingming" placeholder="姓名" readonly></el-input>
				</el-form-item>
			</template>
			<el-form-item class="btn">
				<el-button class="btn3"  v-if="type!='info'" type="success" @click="onSubmit">
					<span class="icon iconfont icon-xihuan"></span>
					提交
				</el-button>
				<el-button class="btn4" v-if="type!='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					取消
				</el-button>
				<el-button class="btn5" v-if="type=='info'" type="success" @click="back()">
					<span class="icon iconfont icon-xihuan"></span>
					返回
				</el-button>
			</el-form-item>
		</el-form>
    

	</div>
</template>
<script>
	import { 
		isIntNumer,
	} from "@/utils/validate";
	export default {
		data() {
			var validateIntNumber = (rule, value, callback) => {
				if(!value){
					callback();
				} else if (!isIntNumer(value)) {
					callback(new Error("请输入整数"));
				} else {
					callback();
				}
			};
			return {
				id: '',
				type: '',
			
			
				ro:{
					yudingbianhao : false,
					changguanmingcheng : false,
					changguanleixing : false,
					changguantupian : false,
					changguanweizhi : false,
					changguanfeiyong : false,
					guanlizhanghao : false,
					guanlixingming : false,
					shijianduan : false,
					zhanghao : false,
					xingming : false,
					sfsh : false,
					shhf : false,
					ispay : false,
				},
			
				ruleForm: {
					yudingbianhao: this.getUUID(),
					changguanmingcheng: '',
					changguanleixing: '',
					changguantupian: '',
					changguanweizhi: '',
					changguanfeiyong: '',
					guanlizhanghao: '',
					guanlixingming: '',
					shijianduan: '',
					zhanghao: '',
					xingming: '',
					shhf: '',
				},
		

				rules: {
					yudingbianhao: [
					],
					changguanmingcheng: [
					],
					changguanleixing: [
					],
					changguantupian: [
					],
					changguanweizhi: [
					],
					changguanfeiyong: [
						{ validator: validateIntNumber, trigger: 'blur' },
					],
					guanlizhanghao: [
					],
					guanlixingming: [
					],
					shijianduan: [
					],
					zhanghao: [
					],
					xingming: [
					],
					sfsh: [
					],
					shhf: [
					],
					ispay: [
					],
				},
			};
		},
		props: ["parent"],
		computed: {



		},
		components: {
		},
		created() {
		},
		methods: {
			// 下载
			download(file){
				window.open(`${file}`)
			},
			// 初始化
			init(id,type) {
				if (id) {
					this.id = id;
					this.type = type;
				}
				if(this.type=='info'||this.type=='else'){
					this.info(id);
				}else if(this.type=='logistics'){
					this.logistics=false;
					this.info(id);
				}else if(this.type=='cross'){
					var obj = this.$storage.getObj('crossObj');
					for (var o in obj){
						if(o=='yudingbianhao'){
							this.ruleForm.yudingbianhao = obj[o];
							this.ro.yudingbianhao = true;
							continue;
						}
						if(o=='changguanmingcheng'){
							this.ruleForm.changguanmingcheng = obj[o];
							this.ro.changguanmingcheng = true;
							continue;
						}
						if(o=='changguanleixing'){
							this.ruleForm.changguanleixing = obj[o];
							this.ro.changguanleixing = true;
							continue;
						}
						if(o=='changguantupian'){
							this.ruleForm.changguantupian = obj[o];
							this.ro.changguantupian = true;
							continue;
						}
						if(o=='changguanweizhi'){
							this.ruleForm.changguanweizhi = obj[o];
							this.ro.changguanweizhi = true;
							continue;
						}
						if(o=='changguanfeiyong'){
							this.ruleForm.changguanfeiyong = obj[o];
							this.ro.changguanfeiyong = true;
							continue;
						}
						if(o=='guanlizhanghao'){
							this.ruleForm.guanlizhanghao = obj[o];
							this.ro.guanlizhanghao = true;
							continue;
						}
						if(o=='guanlixingming'){
							this.ruleForm.guanlixingming = obj[o];
							this.ro.guanlixingming = true;
							continue;
						}
						if(o=='shijianduan'){
							this.ruleForm.shijianduan = obj[o];
							this.ro.shijianduan = true;
							continue;
						}
						if(o=='zhanghao'){
							this.ruleForm.zhanghao = obj[o];
							this.ro.zhanghao = true;
							continue;
						}
						if(o=='xingming'){
							this.ruleForm.xingming = obj[o];
							this.ro.xingming = true;
							continue;
						}
					}
				}
				// 获取用户信息
				this.$http({
					url: `${this.$storage.get('sessionTable')}/session`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						var json = data.data;
						if(((json.zhanghao!=''&&json.zhanghao) || json.zhanghao==0) && this.$storage.get("role")!="管理员"){
							this.ruleForm.zhanghao = json.zhanghao
							this.ro.zhanghao = true;
						}
						if(((json.xingming!=''&&json.xingming) || json.xingming==0) && this.$storage.get("role")!="管理员"){
							this.ruleForm.xingming = json.xingming
							this.ro.xingming = true;
						}
					} else {
						this.$message.error(data.msg);
					}
				});
			
			},
			// 多级联动参数

			info(id) {
				this.$http({
					url: `changguanyuding/info/${id}`,
					method: "get"
				}).then(({ data }) => {
					if (data && data.code === 0) {
						this.ruleForm = data.data;
						//解决前台上传图片后台不显示的问题
						let reg=new RegExp('../../../upload','g')//g代表全部
					} else {
						this.$message.error(data.msg);
					}
				});
			},

			// 提交
			async onSubmit() {
					this.ruleForm.ispay = '未支付'
					if(this.ruleForm.yudingbianhao) {
						this.ruleForm.yudingbianhao = String(this.ruleForm.yudingbianhao)
					}
					if(this.ruleForm.changguantupian!=null) {
						this.ruleForm.changguantupian = this.ruleForm.changguantupian.replace(new RegExp(this.$base.url,"g"),"");
					}
					var objcross = this.$storage.getObj('crossObj');
					await this.$refs["ruleForm"].validate(async valid => {
						if (valid) {
							if(this.type=='cross'){
								var statusColumnName = this.$storage.get('statusColumnName');
								var statusColumnValue = this.$storage.get('statusColumnValue');
								if(statusColumnName!='') {
									var obj = this.$storage.getObj('crossObj');
									if(statusColumnName && !statusColumnName.startsWith("[")) {
										for (var o in obj){
											if(o==statusColumnName){
												obj[o] = statusColumnValue;
											}
										}
										var table = this.$storage.get('crossTable');
										await this.$http({
											url: `${table}/update`,
											method: "post",
											data: obj
										}).then(({ data }) => {});
									}
								}
							}
							
							await this.$http({
								url: `changguanyuding/${!this.ruleForm.id ? "save" : "update"}`,
								method: "post",
								data: this.ruleForm
							}).then(async ({ data }) => {
								if (data && data.code === 0) {
									this.$message({
										message: "操作成功",
										type: "success",
										duration: 1500,
										onClose: () => {
											this.parent.showFlag = true;
											this.parent.addOrUpdateFlag = false;
											this.parent.changguanyudingCrossAddOrUpdateFlag = false;
											this.parent.search();
											this.parent.contentStyleChange();
										}
									});
								} else {
									this.$message.error(data.msg);
								}
							});
						}
					});
			},
			// 获取uuid
			getUUID () {
				return new Date().getTime();
			},
			// 返回
			back() {
				this.parent.showFlag = true;
				this.parent.addOrUpdateFlag = false;
				this.parent.changguanyudingCrossAddOrUpdateFlag = false;
				this.parent.contentStyleChange();
			},
			changguantupianUploadChange(fileUrls) {
				this.ruleForm.changguantupian = fileUrls;
			},
		}
	};
</script>
